@import "../../global.scss";

a { 
    text-decoration: none;
}

.portfolio {
    background-color: var(--alabaster);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--coolGray);
    padding-top: 40px;
    position: relative;

    h1 {
        font-size: 50px;
        font-weight: 200;
        margin-bottom: 20px;

        @include mobile {
            font-size: 35px;
        }

    }

    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile {
            flex-wrap: wrap;
            justify-content: center;
            margin: 10px 0;

        }

        li {
            font-size: 14px;
            font-weight: 400;
            letter-spacing: 0.5px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 5px;
            cursor: pointer;

            &.active {
                background-color: var(--coolGray);
                color: white;

            }
        }
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile {
            width: 100%;
        }

        .item {
                
                margin: 20px;
                color: white;            
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 250px;
                height: 150px;
                border-radius: 10px;
                transition: all 0.5s ease;
                cursor: pointer;

                @include mobile {
                    width: 130px;
                    height: 100px;
                }

            

            img {
                z-index: 1;
                width: 100%;
                height: 100%;
                object-fit: cover;
                
            }
            
            
                h3 {
                    position: absolute;
                    font-size: 20px;
                    font-weight: 300;

                }

                &:hover {
                    background-color: var(--coolGray);
                    img {
                        opacity: 0.2;
                        z-index: 0;
                    }
                }
        }
    }

    .arrow2{
        position: absolute;
        bottom: 100px;
        left: 49%;

        @include mobile {
            display: none;
        }

            .arrowDown {
                font-size: 80px;
                color: var(--coolGray);
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
    }
}